@import "_variables";

*{
    box-sizing: border-box;
    padding: 0;
    margin: 0;
}

ul{
    margin: 0;
    padding: 0;
    list-style-type: none;
}

p{
    margin-bottom: 0;
}
::-webkit-scrollbar{
    width: 5px;
}
::-webkit-scrollbar-track{
    background-color: #dedede;
}
::-webkit-scrollbar-thumb{
    background-color: #828282;
    border-radius: 10px;
}

:focus, :focus-visible, .btn:focus-visible{
    box-shadow: 0 0 0 0.25rem rgba(13, 110, 253, 0.25);
    outline: none !important;
}
body{
    background-color: $secondary-color;
    padding-top: 115px;
    position: relative;
}


.prmry_btn{
    background: $primary-color;
    border-radius: 0;
    transition: all ease 0.3s;    
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    color: $white-color;
    border: 1px solid $white-color;
    svg{
        width: 20px;
        height: 20px;
        fill: $white-color;
        margin-left: 15px;
    }
    img {
        width: 20px;
        height: 20px;
    }
    &:hover, &:focus, &:active{
        background-color: $primary-hover-color !important;
        // color: $white-color !important;
        color: $dark-grey !important;
        border-color: $white-color !important;
        svg{
            fill: $dark-grey;
        }
    }
}

.form-select{
    background: url("data:image/svg+xml;charset=UTF-8,%3csvg xmlns='http://www.w3.org/2000/svg' shape-rendering='geometricPrecision' text-rendering='geometricPrecision' image-rendering='optimizeQuality' fill-rule='evenodd' clip-rule='evenodd' fill='white' width='20' viewBox='0 0 512 266.77'%3e%3cpath fill-rule='nonzero' d='M493.12 3.22c4.3-4.27 11.3-4.3 15.62-.04a10.85 10.85 0 0 1 .05 15.46L263.83 263.55c-4.3 4.28-11.3 4.3-15.63.05L3.21 18.64a10.85 10.85 0 0 1 .05-15.46c4.32-4.26 11.32-4.23 15.62.04L255.99 240.3 493.12 3.22z'/%3e%3c/svg%3e") no-repeat right !important;
    background-position-x: 97% !important;
}
.bootstrap-select>.dropdown-toggle:after{
    width: 25px;
    background-size: 20px !important;
    top: 15px;
}

.bootstrap-select>.dropdown-toggle.bs-placeholder, .bootstrap-select>.dropdown-toggle.bs-placeholder:active, .bootstrap-select>.dropdown-toggle.bs-placeholder:focus, .bootstrap-select>.dropdown-toggle.bs-placeholder:hover, :not(.btn-check) + .btn:active{
    color: $white-color;
    outline: 0 !important;
    background-color: transparent !important;
}
.dropdown-item:active{
    background-color: $primary-color !important;
}

.bootstrap-select .dropdown-menu{
    z-index: 2;
}
.bootstrap-select .dropdown-toggle:focus, .bootstrap-select>select.mobile-device:focus+.dropdown-toggle{
    outline: 0 !important;
    outline-offset: 0 !important;
}

.bootstrap-select:not([class*=col-]):not([class*=form-control]):not(.input-group-btn){
    flex: 1;
    max-width: 100%;
}

.bootstrap-select .dropdown-toggle .filter-option{
    line-height: 40px;
    position: absolute;
    left: 0;
    text-overflow: ellipsis;
    width: 100%;
    overflow: hidden;
    z-index: -1;
    padding: 0 10px;
}

.bootstrap-select:not([class*=col-]):not([class*=form-control]):not(.input-group-btn){
    padding: 0;
    width: 100%;
}
.bootstrap-select>.dropdown-toggle{
    background: transparent;
    border-radius: 0;
    color: $white-color;
    padding: 0 30px 0 10px;
    height: 40px;
    text-overflow: ellipsis;
    z-index: 0;
}

.bootstrap-select.show-tick .dropdown-menu li a span.text{
    text-transform: capitalize;
}

.bootstrap-select>.dropdown-toggle.bs-placeholder{
    color: $white-color;
    font: 400 0.875rem/1rem $Roboto-Regular;
}

.bootstrap-select .bs-ok-default:after{
    content: "\f00c";
    font-family: FontAwesome;
    border-width: 0;
    transform: rotate(0);
    color: $primary-color;
    line-height: 30px;
}

.bootstrap-select .dropdown-menu.inner{
    padding: 5px;
}

.bootstrap-select .dropdown-menu li{
    border-bottom: 1px solid #efefef;
    &:last-child{
        border-bottom: none;
    }
}

.bootstrap-select .dropdown-menu li a{
    padding: 10px 5px;
}

.white_text_colr{
    font: 400 1rem/1.5rem $Roboto-Regular;
