            &.header_item_center{
                text-align: center;
            }
            &.header_item_right{
                text-align: right;
                a{
                    display: flex;
                    align-items: center;
                    justify-content: flex-end;
                    font: 400 1.125rem/1.125rem $Roboto-Regular;
                    color: $white-color;
                    text-decoration: none;
                    cursor: pointer;
                    transition: all ease 0.3s;
                    &:hover, &:focus{
                        color: $primary-color;
                    }
                    label{
                        line-height: 19px;
                        cursor: pointer;
                    }
                }
                em{
                    margin-right: 5px;
                    font-size: 19px;
                }
                & > div{
                    // margin-bottom: 15px;
                    padding-left: 10px;
                    &:last-child{
                        em{
                            margin-right: 0;
                        }
                    }
                }
                
            }
        }
    }
